import "./GalleryCard.scss"
import React from "react"

class GalleryCard extends React.Component {
  constructor() {
    super()
    this.state = {
    }
  }
  render() {
    return(
      <div className="galleryCard row">
        <img loading="lazy" className="cardImage img-fluid" src={this.props.urlImagen} alt="imagen"/>
        <h3 className="cardSubtitle text-center fw-light">{this.props.subtitulo}</h3>
        <p className="cardParagraph text-center">{this.props.parrafo}</p>            
      </div>
    )
  }
}

export default GalleryCard