import "./OurTeam.scss"
import React from "react"
import OurTeamCard from "../../componentes/OurTeamCard/OurTeamCard"
import { Spinner } from "react-bootstrap";

class OurTeam extends React.Component {
  state = {
    ourTeamData: [],
    cargando: true
  }

  componentDidMount() {
    fetch('https://my-json-server.typicode.com/Luis923/json-db/ourTeam')
      .then(res => res.json())
      .then(ourTeamData => this.setState({ ourTeamData, cargando: false }))
      .catch(error => {
        // Manejo del error
      })
  }

  render() {
    if (this.state.cargando) {
      return (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      )
    }
    return (
      <div className="ourTeam p-4">
        <h2 className="ourTeamTitle text-center fs-1">OUR TEAM</h2>
        <div className="container-fluid px-3">
          <div className="row px-5">
            {this.state.ourTeamData.map((ourTeamDat) => (
              <div className="col-12 col-sm-6 col-md-3" key={ourTeamDat.id}>
                <OurTeamCard
                  urlImagen={ourTeamDat.url}
                  subtitulo={ourTeamDat.subtitulo}
                  subtitulo2={ourTeamDat.subtitulo2}
                  parrafo={ourTeamDat.parrafo}
                />
              </div>
            ))}
          </div>
        </div>
      </div>
    )
  }
}

export default OurTeam