import "./PriceTableCard.scss"
import React from "react"

class PriceTableCard extends React.Component {
    constructor() {
        super()

        this.state = {
        }
    }

    render() {
        const styles = {
            background: this.props.color
          }
        return(
            <div className="priceTableCard">
                <img loading="lazy" className="cardImage img-fluid" src={this.props.urlImagen} alt="imagen"/>
                <h3 className="cardSubtitle text-center text-white m-0" style={styles}>{this.props.subtitulo}</h3>
                <h4 className="cardSubtitle2 text-center text-white m-0">{this.props.subtitulo2}</h4>
                <div className="contenideParagraphs py-2">
                    <p className="cardParagraph text-center">{this.props.parrafo}</p>
                    <p className="cardParagraph text-center">{this.props.parrafo2}</p>
                    <p className="cardParagraph text-center">{this.props.parrafo3}</p>
                    <p className="cardParagraph text-center">{this.props.parrafo4}</p>
                    <p className="cardParagraph text-center">{this.props.parrafo5}</p>
                    <p className="cardParagraph text-center">{this.props.parrafo6}</p>
                </div>
                <button className="cardLink d-block text-center w-100 text-white">Buy Now</button>         
            </div>
        )
    }
}

export default PriceTableCard