import React, { Component } from "react";
import "./Footer.scss"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF, faTwitter, faVk } from '@fortawesome/free-brands-svg-icons'
import { faRss } from '@fortawesome/free-solid-svg-icons'

class Footer extends Component{
  render(){
    return(
      <footer className="footer">
        <div className="container">
          <div className="row text-white">
            <div className="col-2">
              <h2 className="footerTitle mt-3">Contact Us</h2>
              <h3 className="footerSubtitle1 fs-5">Adress</h3>
              <p className="footerParagraph1 fw-light">5280 Wetzel Lane U.S.A</p>
              <h3 className="footerSubtitle1 fs-5">Mail</h3>
              <p className="footerParagraph1 fw-light">mail@example.com</p>
              <h3 className="footerSubtitle1 fs-5">Phone</h3>
              <p className="footerParagraph1 fw-light">+5244 125 625</p>
            </div>
            <div className="col-10  d-flex align-items-center justify-content-center">
              <div className="row">
              <p className="copiright col-12 text-center">© 2018 Wellness. All Rights Reserved | Design by W3Layouts</p>
              <ul className="socialMediaList col-12 d-flex justify-content-center p-0">
                <li className="m-2 p-1 rounded-circle bg-light d-flex justify-content-center align-items-center">
                  <FontAwesomeIcon className="socialMedia" icon={faFacebookF} />
                </li>
                <li className="m-2 p-1 rounded-circle bg-light d-flex justify-content-center align-items-center">
                  <FontAwesomeIcon className="socialMedia" icon={faTwitter} />
                </li>
                <li className="m-2 p-1 rounded-circle bg-light d-flex justify-content-center align-items-center">
                  <FontAwesomeIcon className="socialMedia" icon={faRss} />
                </li>
                <li className="m-2 p-1 rounded-circle bg-light d-flex justify-content-center align-items-center">
                  <FontAwesomeIcon className="socialMedia" icon={faVk} />
                </li>
              </ul>
              </div>
            </div>
          </div>
        </div>
      </footer>
    )
  }
}

export default Footer